

import { Injectable } from "@angular/core";
@Injectable()
export class Util {

    public setHeaderAnimation(content: any, header: any, isShow?: boolean) {
        let show: boolean;
        if (isShow === undefined || isShow === null) {
            show = false;
        }
        let hammerTest = new Hammer(content);
        if(!isShow)header.style.transform = "translateY(-100%)";
        header.style.transition = "transform 0.6s"
        hammerTest.get('swipe').set({ direction: Hammer.DIRECTION_VERTICAL });
        hammerTest.on("swipeup", _ => {
            console.info(header)
            header.style.transform = "translateY(-100%)"
        });
        hammerTest.on("swipedown", _ => {
            console.info(header);
            header.style.transform = "translateY(0)"
        });
    }
}


