import { Component, OnInit } from '@angular/core';
import { NavController } from 'ionic-angular';
import { ArtPage } from "../art/art";
import { SearchPage } from "../search/search";
import { MyHttp } from "../../service/Myhttp.service";
import { Config } from '../../service/config.service';
@Component({
  selector: 'page-about',
  templateUrl: 'about.html'
})
export class AboutPage implements OnInit {
  items = [];
  index:number=1;
  ifNext:true;
  maxPage:number=1;
  sildeList=[];
  constructor(public navCtrl: NavController, private mhttp: MyHttp,private config:Config) {
  }

  doInfinite(infiniteScroll) {
    this.getArtList(infiniteScroll)
  }
  openArt(id) {
    this.navCtrl.push(ArtPage,{id:id});
  }
  ngOnInit() {
    this.getArtList(null);
    this.getSilde();
  }

  openSearch() {
    this.navCtrl.push(SearchPage);
  }
  
  getArtList(i:any){
   if(this.index<=this.maxPage) {
    this.mhttp.ajaxPost(this.config.adminUrl+'/ptrs/about', { pages: this.index, rows: 8 }, false).then(resp => {
      let data = eval("(" + resp + ")");
      this.maxPage=parseInt(data.message);
      let list =eval("("+data.content+")") ;
      list.forEach(element => {
        this.putListArt(element)
      });
      this.index++;
      console.info(list)
      if(i!==null&&i!==undefined){ i.complete();}
    })}else{
      i.complete();
    }
  }

  putListArt(art:any){
    let a:ListArt;
    a={
      id:art.articleid,
      title:art.title,
      coverUrl:art.coverpath,
      des:art.description  
    }
    console.info(a)
    this.items.push(a)
  }

  getSilde(){
    this.mhttp.ajaxGet(this.config.adminUrl+"/ptrs/art/show-slide").then(resp=>{
      let data=eval("("+resp+")");
      let sildeObj=eval("("+data.content+")");
      console.info(sildeObj);
      this.sildeList=sildeObj;
    })
  }
  
}
interface ListArt{
  id:number;
  title:string;
  des:string;
  coverUrl:string;
}



