import { Component, OnInit, ViewChild } from '@angular/core';
import { NavController } from 'ionic-angular';
import { ListArtPage } from "../list-art/list-art";
import { ArtPage } from "../art/art";
import { SearchPage } from "../search/search";
import { PushArtPage } from "../push-art/push-art";
import { MyHttp } from '../../service/Myhttp.service';
import { RegisterPage } from '../../pages/register/register';
import { LoginPage } from "../../pages/login/login";
import { AfterViewInit } from '@angular/core/src/metadata/lifecycle_hooks';
import { Config } from '../../service/config.service';
import { Content } from 'ionic-angular';
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage implements OnInit, AfterViewInit {
  coverTitle = "推荐文章";
  artCTitle = "文章分类";
  typeList = [];
  mcoverArt;
  list: any = [];
  topNumber: number;
  image1 = 'https://file.digitaling.com/eImg/uimages/20150907/1441607120885946.gif';
  lazyLoadImage = '';
  @ViewChild('home') content: Content;
  constructor(public navCtrl: NavController, private mhttp: MyHttp, private config: Config) {
    for (let i = 0; i <= 12; i++) {
      this.list.push({});
    }
  }

  openCategoryArt(tid) {
    this.navCtrl.push(ListArtPage, {
      typeid: tid
    });
  }
  openArt() {
    //ArtPage
    this.navCtrl.push(ArtPage, { id: this.mcoverArt.articleid })
  }
  opensearch() {
    this.navCtrl.push(SearchPage)
  }
  openPushArt() {
    this.navCtrl.push(PushArtPage)
  }

  ngOnInit() {
    this.mhttp.ajaxGet(this.config.adminUrl+"/ptrs/chome").then(resp => {
      let data = eval("(" + resp + ")");
      let pageData = eval("(" + data.content + ")");
      this.typeList = pageData[0];
      this.mcoverArt = pageData[1];
      this.lazyLoadImage = this.mcoverArt.coverpath;
      // console.info(pageData[1].coverpath);
      // console.info(pageData);
    })
  }
  startScroll() {
    // console.info(this.topNumber);
    this.topNumber = this.content.scrollTop;
    if (this.topNumber < 2) {
      // console.info("静止")
      document.ontouchmove=function(e){
        // console.info("静止3");
        e.preventDefault();
      }
      // document.addEventListener('touchmove', this.ban, false);
    }
  }
  ngAfterViewInit() {
    let a: any;
    a = document.getElementById("home-bottom");
    a.style.height = (this.config.getTabH() + 25) + "px"
    console.info(a)
    let c: any;
    c = document.querySelector('#home-page')
    let hammer = new Hammer(c);
  }
}



