import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { AboutPage } from '../pages/about/about';
import { ContactPage } from '../pages/contact/contact';
import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { FlexLayoutModule } from "@angular/flex-layout";
// 页面
import { ListArtPageModule } from "../pages/list-art/list-art.module";
import { ArtPageModule } from "../pages/art/art.module";
import { SearchPageModule } from "../pages/search/search.module";
import { PushArtPageModule } from "../pages/push-art/push-art.module";
import {RegisterPageModule} from '../pages/register/register.module';
import { LoginPageModule } from "../pages/login/login.module";
//自定义指令
import { DirectivesModule } from "../directives/directives.module";
import { LazyLoadImageModule  } from "ng-lazyload-image";
//服务
import { MyHttp } from "../service/Myhttp.service";
import { Config } from '../service/config.service';
@NgModule({
  declarations: [
    MyApp,
    AboutPage,
    ContactPage,
    HomePage,
    TabsPage
  ],
  imports: [
    LazyLoadImageModule,
    LoginPageModule,
    PushArtPageModule,
    FlexLayoutModule,
    BrowserModule,
    ListArtPageModule,
    ArtPageModule,
    DirectivesModule,
    SearchPageModule,
    RegisterPageModule,
  
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    AboutPage,
    ContactPage,
    HomePage,
    TabsPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    MyHttp,
    Config,
    { provide: ErrorHandler, useClass: IonicErrorHandler }
  ]
})
export class AppModule { }



