import { Component,OnInit } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ArtPage } from "../art/art";
import { MyHttp } from "../../service/Myhttp.service";
import { Config } from '../../service/config.service';
/**
 * Generated class for the ListArtPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-list-art',
  templateUrl: 'list-art.html',
})
export class ListArtPage implements OnInit {
  items = [];
  type;
  index=1;
  maxPage=1;
  art: any[] = [
    {
      url: 'https://ionicframework.com/dist/preview-app/www/assets/img/nin-live.png'
    }
  ]
  constructor(public navCtrl: NavController, public navParams: NavParams,private mhttp:MyHttp,private config:Config) {
    this.type=navParams.get('typeid');
    console.info(this.type)
  }

  doInfinite(infiniteScroll) {
  }
  


  ngOnInit(){
    this.getArtList(null);
  }

  getArtList(i:any){
    if(this.index<=this.maxPage) {
     this.mhttp.ajaxPost(this.config.adminUrl+'/ptrs/type', { pages: this.index, rows: 8, type:this.type}, false).then(resp => {
       let data = eval("(" + resp + ")");
       this.maxPage=parseInt(data.message);
       let list =eval("("+data.content+")") ;
       list.forEach(element => {
         this.putListArt(element)
       });
       this.index++;
       console.info(list)
       if(i!==null&&i!==undefined){ i.complete();}
     })}else{
       i.complete();
     }
   }
 
   putListArt(art:any){
     let a:ListArt;
     a={
       id:art.articleid,
       title:art.title,
       coverUrl:art.coverpath,
       des:art.description  
     }
     console.info(a)
     this.items.push(a)
   }
  openArt(id) {
    this.navCtrl.push(ArtPage,{id:id})
  }
}
interface ListArt{
  id:number;
  title:string;
  des:string;
  coverUrl:string;
}


