import { NavController } from 'ionic-angular';

@Component({
  selector: 'page-contact',
  templateUrl: 'contact.html'
})
export class ContactPage implements OnInit,AfterViewInit{
  comments=[];
  constructor(public navCtrl: NavController) {
    this.comments.push({});
  }
  ngOnInit(){
    console.info("test")
  
  }
  ngAfterViewInit(){
    this.comments.push({});
    this.comments.push({});
    this.comments.push({});
    this.comments.push({});
  }
  
}



