import { Component,ElementRef,OnInit,ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { MyHttp } from '../../service/Myhttp.service';
import { ArtPage } from '../art/art';
import { Util } from '../../service/util.service';
import { Config } from '../../service/config.service';

/**
 * Generated class for the SearchPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-search',
  templateUrl: 'search.html',
})
export class SearchPage implements OnInit{
  commentList = [];
  keyword = "";
  index = 1;
  maxPage = 1;
  @ViewChild('sh')h :any;
  @ViewChild('sc')c :any;
  constructor(public navCtrl: NavController, public navParams: NavParams, private mhttp: MyHttp,private config:Config,private util:Util) {
    this.commentList.push();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SearchPage');
  }

  search() {
    this.index=1;
    this.maxPage=1;
    this.commentList=[];
    this.getArtList(null);
    
  }

  getArtList(i: any) {
    if (this.index <= this.maxPage) {
      this.mhttp.ajaxPost(this.config.adminUrl+"/ptrs/search", { keyword: this.keyword, pages: this.index }, false).then(resp => {
        let data = eval("(" + resp + ")");
        let content = eval("(" + data.content + ")");
        this.maxPage = parseInt(data.message);
        console.info(content);
        if (content.length > 0) {
          content.forEach(element => {
            this.commentList.push(element);
          });
        }
        this.index++;
        if (i !== null && i !== undefined) { i.complete(); }
      })
    } else {
      i.complete();
    }
  }

  ngOnInit(){
    this.util.setHeaderAnimation(this.c._elementRef.nativeElement,this.h.nativeElement);
  }

  doInfinite(e) {
    this.getArtList(e);
  }

  openArt(id){
    this.navCtrl.push(ArtPage,{id:id})
  }

}



