import { Component, ViewChild, OnInit } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Slides } from 'ionic-angular';
import { MyHttp } from '../../service/Myhttp.service';
import { ToastController } from 'ionic-angular';
import { AfterViewInit } from '@angular/core/src/metadata/lifecycle_hooks';
import { DirectivesModule } from '../../directives/directives.module';
import { Util } from '../../service/util.service';
import { Config } from '../../service/config.service';

/**
 * Generated class for the ArtPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-art',
  templateUrl: 'art.html',
})
export class ArtPage implements OnInit,AfterViewInit {
  @ViewChild('mslide') slides: Slides;
  title = "评论";
  commentList = [];
  contentList = [];
  contentText = '';
  artTitle = '';
  cmContent = '';
  index=1;
  maxpages=1;
  id;
  constructor(public navCtrl: NavController, public navParams: NavParams, private mhttp: MyHttp,public toastCtrl: ToastController,private util:Util,private config:Config) {
    this.id=navParams.get('id');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ArtPage');
  }
  ngOnInit() {
    console.info(this.slides)
    this.mhttp.ajaxPost(this.config.adminUrl+"/ptrs/art/artid", { id: this.id }, false).then(resp => {
      let data = eval("(" + resp + ")");
      console.info(data)
      let content = eval("(" + data.content + ")");
      this.contentList = content;
      this.artTitle = data.title
      this.contentText = content[0].text;
      console.info(content);
    })
    this.getComList(null);
  }
  chengSlide(sile) {
    // let h=0;
    // console.info(sile.realIndex);
    console.info(sile)
    this.contentText = this.contentList[sile.realIndex].text;
    let img = this.slides._slides[sile.realIndex].querySelector('img');

    // let cw=sile.container.offsetWidth
    let iw: number = parseInt(img.getAttribute("img-w"));
    let ih: number = parseInt(img.getAttribute("img-h"));
    let cw = sile.container.offsetWidth;
    let h = parseInt((ih / iw * cw) + "");
    console.info(h);
    sile.container.style.height = h + "px";
  }

  doInfinite(infiniteScroll) {
    this.getComList(infiniteScroll)
  }

  showToast(position: string) {
    let toast = this.toastCtrl.create({
      message: '谢谢客官评价',
      duration: 1500,
      position: position
    });

    toast.present(toast);
  }
  

  getComList(i:any){
    if(this.index<=this.maxpages) {
     this.mhttp.ajaxPost(this.config.adminUrl+'/ptrs/comment/list', { pages: this.index, rows: 3, artid:this.id}, false).then(resp => {
       let data = eval("(" + resp + ")");
       let listComment=eval("("+data.content+")");
       this.maxpages=parseInt(data.message);
       console.info(listComment)
      listComment.forEach(element => {
         this.putListCom(element)
       });
       this.index++;
       if(i!==null&&i!==undefined){ i.complete();}
     })}else{
       i.complete();
     }
   }
 
   putListCom(art:any){
     this.commentList.push(art)
   }

  submit(){
    let data={
      commentid:1,
      commentcontent:this.cmContent,
      commenttime:new Date().getTime(),
      articleid:this.id,
      userid:"tahuazhizaitian",
      username:'他话自在逍遥'
    }

    let sdata=JSON.stringify(data)
    this.mhttp.ajaxPost(this.config.adminUrl+"/ptrs/comment/add",sdata,true).then(resp=>{
      let data=eval("("+resp+")");
      let code=data.code;
      if(code===1){
        this.cmContent=null;
        this.showToast('bottom')
      }
    })
    console.info(this.cmContent)
  }

  ngAfterViewInit(){
    this.util.setHeaderAnimation(document.getElementById("art"),document.getElementById("art-header"))
  }
}



