import { Directive, ElementRef, AfterViewInit,Input,OnChanges} from '@angular/core';
/**
 * Generated class for the LazyImgDirective directive.
 *
 * See https://angular.io/api/core/Directive for more info on Angular
 * Directives.
 */
@Directive({
  selector: '[lazyLoadImg]' // Attribute selector
})
export class LazyImgDirective implements AfterViewInit,OnChanges {
  lazyImg: any;
  @Input('url') url: string;
  constructor(el: ElementRef) {
    // console.info(el.nativeElement.getAttribute("data-src"));
    this.lazyImg = el;
  }

  loadImg(path, img) {
    return new Promise(function (resolve, reject) {
      const image = new Image();
      img.onload = function () {
        let w = image.width;
        let h = image.height;
        resolve([path,w,h])
      }
      img.onerror = function () {
        reject(img)
      }
      image.src = path;
    });
  }
  ngAfterViewInit() {
  }
  initImg(){
    let limg = this.lazyImg;
    this.loadImg(this.url, limg.nativeElement).then((path) => {
      // console.info(path)
      let imgEl = limg.nativeElement;
      imgEl.src = path[0];
      imgEl.setAttribute("img-w", path[1]+"");
      imgEl.setAttribute("img-h", path[2]+"");
    });
  }

  ngOnChanges(){
    // console.info("改变")
    // console.info(this.url);
    this.initImg()
    // console.info("改变")
  }
}



