import { Injectable } from "@angular/core";
@Injectable()
export class Config{
    private domain="http://192.168.0.142";
    private port="8080";
    private tabHeight:number;

    public setTabHeight(height:number){
        this.tabHeight=height;
    }

    public getTabH(){
        return this.tabHeight;
    }

    get adminUrl():string{
        let u=this.domain+":"+this.port;
        return u;
    }
}


