import { Component, ViewChild } from '@angular/core';

import { AboutPage } from '../about/about';
import { ContactPage } from '../contact/contact';
import { HomePage } from '../home/home';
import { AfterViewInit } from '@angular/core/src/metadata/lifecycle_hooks';
import { Config } from '../../service/config.service';


@Component({
  templateUrl: 'tabs.html'
})
export class TabsPage implements AfterViewInit {

  tab1Root = HomePage;
  tab2Root = AboutPage;
  tab3Root = ContactPage;
  nodeTabs: any;
  @ViewChild("t") t: any;
  constructor(private config: Config) {

  }

  ngAfterViewInit() {
    this.initHeight();
    this.initHammer();
  }

  private initHeight() {
    this.nodeTabs=document.getElementById("root-tabs").querySelector('.tabbar');
    this.config.setTabHeight(this.nodeTabs.offsetHeight);
    this.nodeTabs.style.transition = "transform 0.6s";
  }


  private initHammer() {
    let body: any = document.querySelector('body');
    let hammerTest = new Hammer(body);
    hammerTest.get('swipe').set({ direction: Hammer.DIRECTION_VERTICAL, threshold: 5 });
    hammerTest.on("swipeup", _ => {
      console.info(this.nodeTabs)
      this.nodeTabs.style.transform = "translateY(100%)"
    });
    
    hammerTest.on('panup', _ => {
      document.ontouchmove = null;
    });
    
    hammerTest.on("swipedown", _ => {
      console.info(this.nodeTabs)
      console.info(this.nodeTabs.style.transform)
      this.nodeTabs.style.transform = "translateY(0)"
    });
  }
}



